<script>
  import Grid from "./../shared/Grid.svelte";
  import Layout from "../Layout/Layout.svelte";
  import Container from "../shared/Container.svelte";
  import SectionHeading from "../shared/SectionHeading.svelte";
  import { request, gql } from "graphql-request";
  import page from "../stores/page";
  import app from "../stores/app";
  import Select from "svelte-select";
  import * as animateScroll from "svelte-scrollto";
  import { SyncLoader } from "svelte-loading-spinners";
  import { onMount } from "svelte";
  import ClothCard from "../shared/ClothCard.svelte";
  import Header from "../Layout/Header.svelte";

  let pageNumber = 1;
  let pageSize = 10;
  let resultVisible = false;
  let hasMore = true;

  const query = {
    limit: pageSize,
    sortBy: null,
    order: null,
    nom: null,
    pour: $page.pour || null,
    categories: null,
    prixMin: null,
    prixMax: null,
  };

  const findQuery = () => {
    const {
      limit,
      sortBy,
      order,
      nom,
      pour,
      categories,
      prixMin,
      prixMax,
    } = query;

    let _limit = `limit: ${limit}`;
    let _start = `start: ${(pageNumber - 1) * pageSize}`;
    let _sortBy =
      sortBy && order ? `sort: "${sortBy.value}:${order.value}"` : "";
    let _nom = nom ? `nom_contains: "${nom}"` : "";
    let _pour = pour ? `pour_contains: "${pour.value}"` : "";
    let _categories = categories
      ? `categories: { nom_contains: "${categories.value}" }`
      : "";
    let _prix_gte = prixMin ? `prix_gte: ${prixMin}` : "";
    let _prix_lte = prixMax ? `prix_lte: ${prixMax}` : "";

    return gql`
      {
        vetements(
          ${_limit}
          ${_start}
          ${_sortBy}
          where: {
            ${_nom}
            ${_pour}
            ${_categories}
            ${_prix_gte}
            ${_prix_lte}
          }
        ) 
      {
        id
        nom
        prix
        # vues
        tailles {
          taille
        }
        photos {
          url
        }
      }
    }
  `;
  };

  const pourArray = [
    { value: "hommes", label: "hommes" },
    { value: "femmes", label: "femmes" },
    { value: "filles", label: "filles" },
    { value: "garcons", label: "garcons" },
    { value: "bebes", label: "bebes" },
    { value: "neutre", label: "neutre" },
    { value: "", label: "toutes les categories" },
  ];

  const trierParArray = [
    { value: "prix", label: "prix" },
    { value: "nom", label: "nom" },
    { value: "vues", label: "Nombre de vues" },
    { value: "date", label: "date" },
  ];

  const ordreArray = [
    { value: "desc", label: "descendant" },
    { value: "asc", label: "ascendant" },
  ];

  onMount(() => {
    if (query.pour) {
      onRecherche(query);
    }
  });

  const triggerRerender = () => {
    resultVisible = false;

    setTimeout(() => {
      resultVisible = true;
    }, 100);
  };

  const onRecherche = () => {
    pageNumber = 1;
    hasMore = true;
    triggerRerender();
  };

  const onNextPage = () => {
    pageNumber++;
    triggerRerender();
  };

  const onPrevPage = () => {
    pageNumber--;
    if (pageNumber <= 0) pageNumber = 1;
    triggerRerender();
  };

  const checkHasMore = (resultSize) => {
    if (resultSize < pageSize) hasMore = false;
    else hasMore = true;
  };
</script>

<svelte:head>
  <title>Recheche | Bechic Beclass</title>
</svelte:head>

<Layout>
  <Header showOnlyHome />
  <Container>
    <SectionHeading text1="Rechercher" />

    <!-- Formulaire de recherche -->
    <div class="lg:flex lg:items-center">
      <form class="lg:w-7/12" on:submit|preventDefault={onRecherche}>
        <div class="">
          <div
            class="flex flex-col md:flex-row items-center mb-8 justify-center
              text-center md:text-left text-gray-600">
            <i
              class="fas fa-info-circle mb-2 md:mb-0 md:mr-2 fa-2x
                text-brand-strong-green block md:inline-block" />
            <h1 class="md:inline-block">
              Aucun champ n'est obligatoire a remplir. Si vous ne souhaitez pas
              inclure certains criteres dans votre recherche, veuillez laissez
              les champs correspondants vide.
            </h1>
          </div>
        </div>

        <!-- Categories | Pour -->
        <div class="flex flex-col md:flex-row">
          {#await request($app.endpoint, gql`
              {
                categories {
                  value: nom
                  label: nom
                }
              }
            `)}
            <div class="flex items-center justify-center w-full">
              <SyncLoader size="25" color="#F47C01" unit="px" />
              <span class="ml-4">Chargement categories...</span>
            </div>
          {:then res}
            <div class="w-full shadow-sm rounded-lg">
              <Select
                placeholder="Categories"
                items={res.categories}
                bind:selectedValue={query.categories} />
            </div>
          {:catch error}
            {error.message}
          {/await}
          <div class="mt-2 md:mt-0 md:ml-2 w-full shadow-sm rounded-lg">
            <Select
              placeholder="Pour..."
              items={pourArray}
              bind:selectedValue={query.pour} />
          </div>
        </div>
        <!-- Sort By | Order -->
        <div class="mt-2 flex flex-col md:flex-row">
          <div class="w-full shadow-sm rounded-lg">
            <Select
              placeholder="Tri Par"
              items={trierParArray}
              bind:selectedValue={query.sortBy} />
          </div>
          <div class="mt-2 md:mt-0 md:ml-2 w-full shadow-sm rounded-lg">
            <Select
              placeholder="Order"
              items={ordreArray}
              bind:selectedValue={query.order} />
          </div>
        </div>
        <!-- Sort By | Order -->
        <div class="mt-2 flex flex-col md:flex-row">
          <div class="w-full">
            <input
              type="text"
              bind:value={query.prixMin}
              class="shadow-sm"
              placeholder="Prix Minimum (Dollars)" />
          </div>
          <div class="mt-2 md:mt-0 md:ml-2 w-full">
            <input
              type="text"
              bind:value={query.prixMax}
              class="shadow-sm"
              placeholder="Prix Maximum (Dollars)" />
          </div>
        </div>
        <!-- Sort By | Order -->
        <div class="mt-2 flex flex-col md:flex-row">
          <div class="w-full">
            <input
              type="text"
              class="shadow-sm"
              bind:value={query.nom}
              placeholder="Nom ou model" />
          </div>
        </div>
        <!-- Submit -->
        <button
          class="shadow-sm w-full rounded-lg bg-gradient-to-r
            from-brand-strong-green to-brand-weaker-green text-center py-3 mt-2
            text-gray-100 flex items-center justify-center">
          <i class="fas fa-search mr-3" /> Rechercher
        </button>
      </form>

      <div
        class="p-4 mt-8 md:w-2/3 md:mx-auto lg:mx-0 lg:mt-0 lg:w-5/12 lg:p-16
          lg:block">
        <img
          class="w-full h-full"
          src="./images/window-shop.svg"
          alt="hero lady" />
      </div>
    </div>

    <!-- Results -->
    <div class="h-2" id="resultats-recherche" />
    {#if resultVisible}
      <div class="mt-5">
        {#await request($app.endpoint, findQuery())}
          <!-- WAITING FOR RESULTS... -->
          <div class="hidden funcEl">
            {animateScroll.scrollTo({ element: '#resultats-recherche' })}
          </div>
          <div
            class="flex justify-center items-center"
            style="height: 110vh;"
            id="chargement">
            <SyncLoader size="100" color="#E76F51" unit="px" />
          </div>
        {:then res}
          <!-- GOT THE RESULTS -->
          <div class="hidden funcEl">
            {animateScroll.scrollTo({ element: '#resultats-recherche' })}
          </div>
          <!-- If there is nothing more to fetch -->
          {#if res.vetements.length == 0 && pageNumber > 1}
            <h1
              class="flex items-center justify-center mt-16 mb-2 mx-1 md:mx-auto
                px-4 py-8 bg-gradient-to-r md:w-128 from-brand-strong-orange
                to-brand-weaker-orange rounded-md shadow-md text-gray-100">
              <i class="fas fa-info-circle mr-4 tracking-normal fa-2x" /> Plus Aucun
              Resultats
            </h1>
            <!-- If there were no results in the first place -->
          {:else if res.vetements.length == 0}
            <h1
              class="flex items-center justify-center mt-16 mb-2 mx-1 md:mx-auto
                px-4 py-8 bg-gradient-to-r md:w-128 from-brand-strong-red
                to-brand-weak-red rounded-md shadow-md text-gray-100">
              <i class="fas fa-info-circle mr-4 tracking-normal fa-2x" /> Aucun Resultats
            </h1>
            <!-- If we get some results -->
          {:else}
            <!-- We've got to make sure there's nothing more to fetch -->
            <div class="hidden funcEl">
              {checkHasMore(res.vetements.length)}
            </div>
            <h1 class="text-4xl mt-12 mb-6 text-center">
              Resultats de la recherche
            </h1>
            <Grid>
              {#if res.vetements.length < 3}
                {#each res.vetements as vetement (vetement.id)}
                  <ClothCard {...vetement} fixed />
                {/each}
              {:else}
                {#each res.vetements as vetement (vetement.id)}
                  <ClothCard {...vetement} />
                {/each}
              {/if}
            </Grid>
            <div
              class="flex flex-col sm:flex-row mt-8 items-center justify-center">
              {#if pageNumber > 1}
                <button
                  on:click={onPrevPage}
                  class="shadow-sm w-full mb-2 sm:mb-0 sm:mr-4 sm:w-40
                    rounded-lg bg-gradient-to-r from-brand-strong-green
                    to-brand-weaker-green text-center py-3 text-gray-100 flex
                    items-center justify-center">
                  <i class="fas fa-arrow-left mr-2" /> Page Precedente
                </button>
              {/if}
              {#if hasMore}
                <button
                  on:click={onNextPage}
                  class="shadow-sm w-full sm:w-40 rounded-lg bg-gradient-to-r
                    from-brand-strong-green to-brand-weaker-green text-center
                    py-3 text-gray-100 flex items-center justify-center">
                  Page Suivante <i class="fas fa-arrow-right ml-2" />
                </button>
              {/if}
            </div>
          {/if}
        {:catch error}
          <h1
            class="flex items-center mt-4 mb-2 mx-1 md:mx-auto px-4 py-8
              bg-gradient-to-r md:w-128 from-brand-strong-red to-brand-weak-red
              rounded-md shadow-md text-gray-100">
            <i class="fas fa-info-circle mr-4 tracking-normal fa-2x" />
            {error.message}
          </h1>
        {/await}
      </div>
    {/if}

    <div class="py-16" />
  </Container>
</Layout>
