<script>
  import { link } from "svelte-spa-router";
  import { cart, cartUtils } from "../stores/cart";
  export let id;
  export let nom;
  export let prix;
  export let qty;
  export let url;
  $: total = qty * prix;

  $: {
    if (!qty) qty = 1;
    else if (qty < 1) qty = 1;
    else if (qty > 20) qty = 20;
  }

  const onDelete = () => {
    cart.remove(id);
  };

  const incrementCount = () => {
    cart.increment(id);
  };

  const decrementCount = () => {
    cart.decrement(id);
  };
</script>

<!-- Desktop & Tablets -->
<div
  class="hidden md:flex items-center py-4 px-6 rounded-lg bg-white shadow-sm
    mt-2">
  <div class="w-2/12">
    <img class="w-32 object-cover rounded-md" src={url} alt={id} />
  </div>
  <div class="w-2/12 p-2 capitalize">{nom}</div>
  <div class="w-1/12 p-2 text-xl text-center text-brand-strong-orange">
    ${prix}
  </div>
  <div class="w-2/12 p-2 text-xl text-center text-brand-weak-green">
    ${total}
  </div>
  <div class="w-4/12 p-2 flex items-center">
    <button
      on:click={decrementCount}
      class="shadow-md bg-gradient-to-r from-brand-strong-green
        to-brand-weaker-green py-2 px-5 rounded-md text-gray-100 mr-2">-</button>
    <input class="shadow-sm" type="number" min="1" max="20" bind:value={qty} />
    <button
      on:click={incrementCount}
      class="shadow-md bg-gradient-to-r from-brand-strong-green
        to-brand-weaker-green py-2 px-5 rounded-md text-gray-100 ml-2">+</button>
    <a
      href={`/product/${id}`}
      use:link
      class="hidden lg:flex ml-4 items-center justify-center bg-gradient-to-r
        from-brand-strong-orange to-brand-weaker-orange px-3 py-3 rounded-md
        shadow-md text-white">
      <i class="fas fa-eye" /></a>
  </div>
  <div class="w-1/12 p-2 flex items-center">
    <button
      on:click={onDelete}
      class="flex items-center justify-center bg-gradient-to-r
        from-brand-strong-red to-brand-weak-red py-3 rounded-md shadow-md w-full
        text-white">
      <i class="fas fa-trash" /></button>
  </div>
</div>

<!-- Mobile -->
<div
  class="flex md:hidden flex-col items-center py-4 px-3 rounded-lg bg-white
    shadow-sm mt-4">
  <div class="w-full flex items-center">
    <img class="w-32 object-cover rounded-md" src={url} alt={id} />
    <div class="w-full ml-3">
      <div class="capitalize">{nom}</div>
      <div class="mt-3 flex items-center text-brand-strong-orange">
        <div class="w-20">Prix:</div>
        ${prix}
      </div>
      <div class="mt-3 flex items-center text-brand-weak-green">
        <div class="w-20">Total:</div>
        ${total}
      </div>
    </div>
  </div>

  <div class="w-full mt-4 flex items-center">
    <div class="mr-4">Quantité:</div>
    <button
      on:click={decrementCount}
      class="shadow-md bg-gradient-to-r from-brand-strong-green
        to-brand-weaker-green py-2 px-5 rounded-md text-gray-100">-</button>
    <input
      class="shadow-sm mx-2"
      type="number"
      min="1"
      max="20"
      bind:value={qty} />
    <button
      on:click={incrementCount}
      class="shadow-md bg-gradient-to-r from-brand-strong-green
        to-brand-weaker-green py-2 px-5 rounded-md text-gray-100">+</button>
  </div>

  <div class="w-full mt-5 flex items-center">
    <button
      on:click={onDelete}
      class="flex items-center justify-center bg-gradient-to-r
        from-brand-strong-red to-brand-weak-red py-2 rounded-md shadow-md w-full
        text-white">
      <i class="fas fa-trash mr-2" />Supprimer</button>
  </div>

  <div class="w-full mt-2 flex items-center">
    <a
      href={`/product/${id}`}
      use:link
      class="flex items-center justify-center bg-gradient-to-r
        from-brand-strong-green to-brand-weaker-green py-2 rounded-md shadow-md
        w-full text-white">
      <i class="fas fa-eye mr-2" />Voir Details</a>
  </div>
</div>
