<script>
  import { onMount } from "svelte";
  import { link } from "svelte-spa-router";
  import { cart, cartUtils } from "../stores/cart";

  export let id;
  export let nom = "Model Non Defini";
  export let prix = 0.0;
  export let photos = [];
  export let tailles = [];
  export let fixed = false;
  let isInCart = false;

  const onAddToCart = () => {
    let item = { id, nom, qty: 1, prix, url: photos[0].url };
    cart.add(item);
    isInCart = true;
  };

  const onRemoveFromCart = () => {
    cart.remove(id);
    isInCart = false;
  };

  onMount(() => {
    isInCart = cartUtils.hasItem(id);
  });
</script>

<div
  class={`shadow-md w-full rounded-lg bg-white flex flex-col justify-between min-h-screen ${fixed && 'md:w-1/3'}`}>
  <div class="">
    <div
      class="w-full h-72 rounded-lg rounded-b-none"
      style={`background: url(${photos[0].url}); background-size:cover;`} />

    <div class="py-5 px-4 relative">
      <div class="flex justify-between">
        <h1 class="text-lg mr-6">{nom}</h1>
        <span
          class="text-2xl fill-current font-bold bg-gradient-to-r
            from-brand-strong-green to-brand-weaker-green bg-clip-text
            text-transparent">${prix.toFixed(2)}</span>
      </div>

      {#if tailles}
        <div class="flex flex-wrap">
          {#each tailles as taille}
            <div
              class="mr-3 mt-3 px-3 rounded-full border border-gray-400
                text-brand-weaker-green text-sm uppercase tracking-wider
                bg-gray-100">
              {taille.taille}
            </div>
          {/each}
        </div>
      {/if}
    </div>
  </div>
  <div class="pb-5 px-4 mt-5">
    {#if isInCart}
      <button
        on:click={onRemoveFromCart}
        class="flex items-center btn-blue"
        style="width: 100%;">
        <i class="fas fa-minus-circle mr-3" /> Retirer du Panier
      </button>
    {:else}
      <button
        on:click={onAddToCart}
        class="flex items-center btn-primary"
        style="width: 100%;">
        <i class="mr-3 fas fa-cart-plus" /> Ajouter Au Panier
      </button>
    {/if}
    <a
      href={`/product/${id}`}
      style="width: 100%;"
      class="flex items-center btn-secondary mt-4"
      use:link>
      <i class="mr-3 fas fa-eye" /> Voir Details
    </a>
  </div>
</div>
