<script>
  import Container from "../shared/Container.svelte";
  import MobileMenu from "./MobileMenu.svelte";
  import { link } from "svelte-spa-router";
  import { onMount } from "svelte";
  export let showOnlyHome = false;

  onMount(() => {
    window.onscroll = (e) => {
      scrollFunction();
    };

    const scrollFunction = () => {
      let innerHeader = document.getElementById("inner-header");
      let header = document.getElementById("header");

      if (
        document.body.scrollTop > 40 ||
        document.documentElement.scrollTop > 40
      ) {
        innerHeader.classList.replace("h-16", "h-12");
        header.classList.add("shadow-sm");
        header.classList.add("bg-gray-100");
      } else {
        innerHeader.classList.replace("h-12", "h-16");
        header.classList.remove("shadow-sm");
        header.classList.remove("bg-gray-100");
      }
    };
  });
</script>

<div
  id="header"
  class="navbar text-gray-700 fixed top-0 left-0 w-full z-50 transition-all
    duration-250">
  <Container>
    <div
      id="inner-header"
      class="h-16 flex justify-between items-center transition-all duration-250">
      <a class="nav-link" href="#home"> <i class="fas fa-home text-xl" /> </a>

      {#if !showOnlyHome}
        <div class="hidden lg:flex">
          <a class="nav-link mr-6" href="/search" use:link>Shopping</a>
          <a class="nav-link mr-6" href="#collection">Categories</a>
          <a class="nav-link mr-6" href="#recent">Arrivages</a>
        </div>
      {/if}

      <div class="lg:flex lg:items-center">
        {#if !showOnlyHome}
          <a class="hidden lg:block nav-link mr-8 lg:mr-0" href="#contact">
            Contactez-Nous
          </a>
        {/if}
        <a
          class="hidden lg:flex lg:items-center lg:justify-center nav-link ml-6"
          href="/search"
          use:link>
          <i class="fas fa-search mr-1 text-xl" />
        </a>
        <a
          class="hidden lg:flex lg:items-center lg:justify-center nav-link ml-6"
          href="/cart"
          use:link>
          <i class="fas fa-shopping-bag mr-1 text-xl" />
        </a>
        <MobileMenu {showOnlyHome} />
      </div>
    </div>
  </Container>
</div>
