import { request, gql } from 'graphql-request';

const Order = {
  send: async (endpoint, data) => {
    const query = gql`
      mutation CreateCommande($data: CommandeInput!) {
        createCommande(input : {data: $data}){
          commande{
            # ORDER
            id
            date
            contenu{
              vetement{
                nom
              }
              quantite
            }
            totalSansLivraison
            fraisLivraison
            totalAvecLivraison
            encours
            # CLIENT
            nom
            telephone
            addresse
            email
            commentaire
          }
        }
      }
    `;

    const { nom, telephone, email, addresse, contenu, info } = data;
    const variables = {
      data: {
        nom,
        telephone,
        email,
        addresse,
        commentaire: info,
        contenu
      }
    }
    return await request(endpoint, query, variables);
  }
}

export default Order;