import { writable } from 'svelte/store';
import * as ls from "local-storage";

const createCart = () => {
  const { subscribe, set, update } = writable(ls.get('cart') || []);

  return {
    subscribe,
    add: (item) => update(prev => {
      let inCart = ls.get("cart");

      if (!inCart) {
        ls.set("cart", [item]);
        return ls.get("cart");
      }
      else if (inCart.length >= 20) {
        alert("Vous ne pouvez pas avoir plus de 20 items dans votre panier");
        return ls.get("cart");
      }
      else {
        ls.set("cart", [...inCart, item]);
        return ls.get("cart");
      };
    }),
    remove: (id) => update(prev => {
      let inCart = ls.get("cart");
      ls.set(
        "cart",
        inCart.filter(item => item.id != id)
      );
      return ls.get("cart");
    }),
    increment: (id) => update(prev => {
      let inCart = ls.get('cart');
      let index = inCart.findIndex(item => item.id == id);
      inCart[index].qty++;
      ls.set('cart', inCart);
      return ls.get("cart");
    }),
    decrement: (id) => update(prev => {
      let inCart = ls.get('cart');
      let index = inCart.findIndex(item => item.id == id);
      inCart[index].qty--;
      ls.set('cart', inCart);
      return ls.get("cart");
    }),
    reset: () => set(() => {
      ls.remove('cart');
      return [];
    }),
  };
}

export const cart = createCart();

export const cartUtils = {
  hasItem: id => {
    let inCart = ls.get("cart");
    if (inCart) {
      if (inCart.find(item => item.id === id)) return true;
      else return false;
    } else return false;
  },
}