<script>
  import Router from "svelte-spa-router";
  import routes from "./routes";
  import { request, gql } from "graphql-request";
  import { onMount } from "svelte";
  import app from "./stores/app";

  onMount(() => {
    request(
      $app.endpoint,
      gql`
        {
          parametre {
            email
            addresse
            telephone
            horaire
            fraisLivraison
          }
        }
      `
    ).then((res) => {
      app.update((prev) => {
        return {
          ...prev,
          meta: res.parametre,
        };
      });
    });
  });
</script>

<Router {routes} />
