<script>
  import app from "../stores/app";
  import { flip } from "svelte/animate";
  import { cart } from "../stores/cart";
  import { link } from "svelte-spa-router";
  import Layout from "../Layout/Layout.svelte";
  import Header from "./../Layout/Header.svelte";
  import CartItem from "../shared/CartItem.svelte";
  import Container from "../shared/Container.svelte";
  import OrderForm from "../shared/OrderForm.svelte";
  import { SyncLoader } from "svelte-loading-spinners";
  import SectionHeading from "../shared/SectionHeading.svelte";
  import CartItemHeader from "../shared/CartItemHeader.svelte";

  $: sum = $cart
    ? $cart.reduce((acc, item) => acc + item.qty * item.prix, 0)
    : 0;

  let showModal = false;

  const onOpenModal = () => {
    showModal = true;
  };
  const onCloseModal = () => {
    showModal = false;
  };
</script>

<OrderForm show={showModal} on:orderModalClose={onCloseModal} />

<Layout>
  <Header showOnlyHome />
  <Container>
    <SectionHeading text1="Votre panier" />

    {#if $cart.length > 0}
      <CartItemHeader />
      {#each $cart as item (item.id)}
        <div animate:flip={{ duration: 200 }}>
          <CartItem {...item} />
        </div>
      {/each}

      <div
        class="flex flex-col justify-center items-center sm:flex-row
          sm:justify-between sm:items-center p-5 shadow-sm rounded-lg mt-8
          bg-white">
        {#if $app.meta}
          <div class="w-full">
            <h1
              class="text-2xl text-gray-700 uppercase tracking-wide text-center
                sm:text-left">
              Total : ${sum + $app.meta.fraisLivraison}
            </h1>
            <div
              class="text-sm uppercase mt-2 text-center text-brand-strong-green
                sm:text-left">
              * Ce montant inclu ${$app.meta.fraisLivraison} de frais livraison
            </div>
          </div>
        {:else}
          <div class="flex items-center w-full">
            <SyncLoader size="25" color="#F47C01" unit="px" />
            <span class="ml-4">Calcul Somme Totale, Veuillez Patienter</span>
          </div>
        {/if}
        <div class="w-full mt-4 sm:mt-0">
          <button
            on:click={onOpenModal}
            class="btn-primary flex items-center justify-center"
            style="width: 100%">
            Passer la commande <i class="ml-3 fas fa-external-link-square-alt" />
          </button>
        </div>
      </div>
    {:else}
      <div
        class="flex flex-col items-center justify-center"
        style="height: 60vh;">
        <div class="">
          <h1 class="mb-6 text-center text-xl">Votre panier est vide</h1>
          <a
            href="/search"
            class="btn-tertiary flex items-center justify-center"
            use:link>
            Faire du shopping <i class="fas fa-cart-plus" />
          </a>
        </div>
      </div>
    {/if}
  </Container>
</Layout>
