<script>
  import { link } from "svelte-spa-router";
  import ClothCard from "../shared/ClothCard.svelte";
  import Container from "../shared/Container.svelte";
  import Carousel from "@beyonk/svelte-carousel";
  import SectionHeading from "../shared/SectionHeading.svelte";
  import { request, gql } from "graphql-request";
  import { SyncLoader } from "svelte-loading-spinners";
  import app from "../stores/app";
  import page from "../stores/page";

  let setPageQueryValue = (pour) => {
    page.set({ pour });
  };

  const latestAll = gql`
    {
      all: vetements(sort: "createdAt:desc", limit: 10) {
        id
        nom
        prix
        tailles {
          taille
        }
        photos {
          url
        }
      }
      femmes: vetements(
        sort: "createdAt:desc"
        limit: 10
        where: { pour_contains: "femmes" }
      ) {
        id
        nom
        prix
        tailles {
          taille
        }
        photos {
          url
        }
      }
      hommes: vetements(
        sort: "createdAt:desc"
        limit: 10
        where: { pour_contains: "hommes" }
      ) {
        id
        nom
        prix
        tailles {
          taille
        }
        photos {
          url
        }
      }
      filles: vetements(
        sort: "createdAt:desc"
        limit: 10
        where: { pour_contains: "filles" }
      ) {
        id
        nom
        prix
        tailles {
          taille
        }
        photos {
          url
        }
      }
      garcons: vetements(
        sort: "createdAt:desc"
        limit: 10
        where: { pour_contains: "garcons" }
      ) {
        id
        nom
        prix
        tailles {
          taille
        }
        photos {
          url
        }
      }
      bebes: vetements(
        sort: "createdAt:desc"
        limit: 10
        where: { pour_contains: "bebes" }
      ) {
        id
        nom
        prix
        tailles {
          taille
        }
        photos {
          url
        }
      }
    }
  `;
</script>

<Container>
  <SectionHeading text1="Ajoutés Recemment" id="recent" />

  {#await request($app.endpoint, latestAll)}
    <div class="flex justify-center items-center h-screen">
      <SyncLoader size="100" color="#E76F51" unit="px" />
    </div>
  {:then res}
    <h1
      class="flex items-center mb-8 mx-1 p-3 bg-gradient-to-r
        from-brand-strong-pink to-brand-weak-pink rounded-md shadow-md
        text-gray-100 md:w-128">
      <i class="fas fa-info-circle mr-4 tracking-normal fa-2x" /> Glisser a gauche
      ou a droite pour voir plus de photos
    </h1>

    <!-- Toutes les categories -->
    {#if res.all.length > 0}
      <div class="">
        <h1
          class="mx-1 text-xl mb-8 pl-2 border-l-2 border-brand-strong-orange
            flex items-center">
          Derniers Arrivages <span class="ml-3 text-brand-strong-orange text-2xl">&gt;</span>
        </h1>

        <Carousel
          autoplay={15000}
          perPage={{ 1550: 5, 1250: 3, 950: 3, 650: 2, 350: 1 }}
          dots={false}>
          {#each res.all as vetement, i (vetement.id)}
            <div class="p-2">
              <ClothCard {...vetement} />
            </div>
          {:else}
            <h1
              class="flex items-center mt-4 mb-2 mx-1 px-3 py-4 bg-gradient-to-r
                md:w-128 from-brand-strong-green to-brand-weaker-green
                rounded-md shadow-md text-gray-100">
              <i class="fas fa-info-circle mr-4 tracking-normal fa-2x" /> Pour Bientot...
            </h1>
          {/each}
        </Carousel>

        <a
          href="/search"
          use:link
          on:click={() => setPageQueryValue('')}
          class="ml-2 mr-2 sm:mr-0 mt-3 sm:w-auto block sm:inline-block px-4
            py-3 text-center bg-gradient-to-r from-brand-strong-green
            to-brand-weaker-green rounded-md text-gray-100">
          Voir toutes nos categories <span class="ml-3">&gt;</span>
        </a>
      </div>
    {/if}

    <!-- Derniers arrivages femmes -->
    {#if res.femmes.length > 0}
      <div class="">
        <h1
          class="mx-1 text-xl mt-32 mb-8 pl-2 border-l-2
            border-brand-strong-orange flex items-center">
          Derniers arrivages femmes <span class="ml-3 text-brand-strong-orange
              text-2xl">&gt;</span>
        </h1>

        <Carousel
          autoplay={15000}
          perPage={{ 1550: 5, 1250: 3, 950: 3, 650: 2, 350: 1 }}
          dots={false}>
          {#each res.femmes as vetement, i (vetement.id)}
            <div class="p-2">
              <ClothCard {...vetement} />
            </div>
          {:else}
            <h1
              class="flex items-center mt-4 mb-2 mx-1 px-3 py-4 bg-gradient-to-r
                md:w-128 from-brand-strong-green to-brand-weaker-green
                rounded-md shadow-md text-gray-100">
              <i class="fas fa-info-circle mr-4 tracking-normal fa-2x" /> Pour Bientot...
            </h1>
          {/each}
        </Carousel>

        <a
          href="/search"
          use:link
          on:click={() => setPageQueryValue('femmes')}
          class="ml-2 mr-2 sm:mr-0 mt-3 sm:w-auto block sm:inline-block px-4
            py-3 text-center bg-gradient-to-r from-brand-strong-green
            to-brand-weaker-green rounded-md text-gray-100">
          Voir tout dans cette categorie <span class="ml-3">&gt;</span>
        </a>
      </div>
    {/if}

    <!-- Derniers arrivages hommes -->
    {#if res.hommes.length > 0}
      <div class="">
        <h1
          class="mx-1 text-xl mt-32 mb-8 pl-2 border-l-2
            border-brand-strong-orange flex items-center">
          Derniers arrivages hommes <span class="ml-3 text-brand-strong-orange
              text-2xl">&gt;</span>
        </h1>

        <Carousel
          autoplay={15000}
          perPage={{ 1550: 5, 1250: 3, 950: 3, 650: 2, 350: 1 }}
          dots={false}>
          {#each res.hommes as vetement, i (vetement.id)}
            <div class="p-2">
              <ClothCard {...vetement} />
            </div>
          {:else}
            <h1
              class="flex items-center mt-4 mb-2 mx-1 px-3 py-4 bg-gradient-to-r
                md:w-128 from-brand-strong-green to-brand-weaker-green
                rounded-md shadow-md text-gray-100">
              <i class="fas fa-info-circle mr-4 tracking-normal fa-2x" /> Pour Bientot...
            </h1>
          {/each}
        </Carousel>

        <a
          href="/search"
          use:link
          on:click={() => setPageQueryValue('hommes')}
          class="ml-2 mr-2 sm:mr-0 mt-3 sm:w-auto block sm:inline-block px-4
            py-3 text-center bg-gradient-to-r from-brand-strong-green
            to-brand-weaker-green rounded-md text-gray-100">
          Voir tout dans cette categorie <span class="ml-3">&gt;</span>
        </a>
      </div>
    {/if}

    <!-- Derniers filles -->
    {#if res.filles.length > 0}
      <div class="">
        <h1
          class="mx-1 text-xl mt-32 mb-8 pl-2 border-l-2
            border-brand-strong-orange flex items-center">
          Arrivages filles <span class="ml-3 text-brand-strong-orange text-2xl">&gt;</span>
        </h1>

        <Carousel
          autoplay={15000}
          perPage={{ 1550: 5, 1250: 3, 950: 3, 650: 2, 350: 1 }}
          dots={false}>
          {#each res.filles as vetement, i (vetement.id)}
            <div class="p-2">
              <ClothCard {...vetement} />
            </div>
          {/each}
        </Carousel>

        <a
          href="/search"
          use:link
          on:click={() => setPageQueryValue('filles')}
          class="ml-2 mr-2 sm:mr-0 mt-3 sm:w-auto block sm:inline-block px-4
            py-3 text-center bg-gradient-to-r from-brand-strong-green
            to-brand-weaker-green rounded-md text-gray-100">
          Voir tout dans cette categorie <span class="ml-3">&gt;</span>
        </a>
      </div>
    {/if}

    <!-- Derniers garcons -->
    {#if res.garcons.length > 0}
      <div class="">
        <h1
          class="mx-1 text-xl mt-32 mb-8 pl-2 border-l-2
            border-brand-strong-orange flex items-center">
          Arrivages garcons <span class="ml-3 text-brand-strong-orange text-2xl">&gt;</span>
        </h1>

        <Carousel
          autoplay={15000}
          perPage={{ 1550: 5, 1250: 3, 950: 3, 650: 2, 350: 1 }}
          dots={false}>
          {#each res.garcons as vetement, i (vetement.id)}
            <div class="p-2">
              <ClothCard {...vetement} />
            </div>
          {/each}
        </Carousel>

        <a
          href="/search"
          use:link
          on:click={() => setPageQueryValue('garcons')}
          class="ml-2 mr-2 sm:mr-0 mt-3 sm:w-auto block sm:inline-block px-4
            py-3 text-center bg-gradient-to-r from-brand-strong-green
            to-brand-weaker-green rounded-md text-gray-100">
          Voir tout dans cette categorie <span class="ml-3">&gt;</span>
        </a>
      </div>
    {/if}

    <!-- Derniers bebes -->
    {#if res.bebes.length > 0}
      <div class="">
        <h1
          class="mx-1 text-xl mt-32 mb-8 pl-2 border-l-2
            border-brand-strong-orange flex items-center">
          Arrivages bébés <span class="ml-3 text-brand-strong-orange text-2xl">&gt;</span>
        </h1>

        <Carousel
          autoplay={15000}
          perPage={{ 1550: 5, 1250: 3, 950: 3, 650: 2, 350: 1 }}
          dots={false}>
          {#each res.bebes as vetement, i (vetement.id)}
            <div class="p-2">
              <ClothCard {...vetement} />
            </div>
          {/each}
        </Carousel>

        <a
          href="/search"
          use:link
          on:click={() => setPageQueryValue('bebes')}
          class="ml-2 mr-2 sm:mr-0 mt-3 sm:w-auto block sm:inline-block px-4
            py-3 text-center bg-gradient-to-r from-brand-strong-green
            to-brand-weaker-green rounded-md text-gray-100">
          Voir tout dans cette categorie <span class="ml-3">&gt;</span>
        </a>
      </div>
    {/if}
  {:catch error}
    <h1
      class="flex items-center mt-4 mb-2 mx-1 md:mx-auto px-4 py-8
        bg-gradient-to-r md:w-128 from-brand-strong-red to-brand-weak-red
        rounded-md shadow-md text-gray-100">
      <i class="fas fa-info-circle mr-4 tracking-normal fa-2x" />
      {error.message}
    </h1>
  {/await}
</Container>
