<script>
  import Carousel from "@beyonk/svelte-carousel";
  import Layout from "../Layout/Layout.svelte";
  import Container from "../shared/Container.svelte";
  import SectionHeading from "../shared/SectionHeading.svelte";
  import { SyncLoader } from "svelte-loading-spinners";
  import { request, gql } from "graphql-request";
  import app from "../stores/app";
  import Header from "../Layout/Header.svelte";
  import { onMount } from "svelte";
  import * as animateScroll from "svelte-scrollto";
  import { cart, cartUtils } from "../stores/cart";
  export let params;

  // add vues here

  const query = gql`
    {
    vetement(id : "${params.id}"){
      id
      nom
      prix
      pour
      vues
      categories{
        nom
      }
      tailles{
        taille
      }
      photos{
        url
      }
    }
  }
  `;

  onMount(() => {
    animateScroll.scrollTo({ element: "#top" });
    isInCart = cartUtils.hasItem(params.id);
  });

  let isInCart = false;

  const onAddToCart = (item) => {
    cart.add(item);
    isInCart = true;
  };

  const onRemoveFromCart = () => {
    cart.remove(params.id);
    isInCart = false;
  };

  const setPageTitle = (nom, pour) => {
    document.title = `${nom} pour ${pour} | Bechic Beclass Boutique`;
  };
</script>

<Layout>
  <Header showOnlyHome />
  <Container>
    <div class="min-h-screen" id="top">
      {#await request($app.endpoint, query)}
        <div
          class="flex justify-center items-center"
          style="height: 100vh;"
          id="chargement">
          <SyncLoader size="100" color="#02C39A" unit="px" />
        </div>
      {:then res}
        <div class="hidden funcEl">
          {setPageTitle(res.vetement.nom, res.vetement.pour)}
        </div>
        <div class="flex flex-col md:flex-row pt-20">
          <div class="w-full md:w-1/2 lg:w-1/2 rounded-lgsm:mt-0">
            {#if res.vetement.photos.length == 1}
              <img
                class="object-cover object-center w-full shadow-sm rounded-lg"
                src={res.vetement.photos[0].url}
                alt={res.vetement.model} />
            {:else}
              <Carousel perPage={1} dots={false}>
                {#each res.vetement.photos as photo, i (i)}
                  <div key={res.vetement.id} class="m-1">
                    <img
                      class="object-cover object-center w-full shadow-sm
                        rounded-lg"
                      src={photo.url}
                      alt={res.vetement.model} />
                  </div>
                {/each}
              </Carousel>
            {/if}
          </div>
          <div class="pl-2 md:pl-4 lg:pl-8 w-full md:w-1/2 lg:w-1/2 rounded-lg">
            {#if res.vetement.photos.length > 1}
              <div class="mt-2 py-4 text-gray-600 text-center animate-pulse">
                Glissez a gauche ou a droite pour voir plus de photos
              </div>
            {/if}
            <!-- Nom -->
            <h1 class="uppercase text-3xl text-gray-700 tracking-wide mt-4">
              {res.vetement.nom}
            </h1>

            <!-- Prix -->
            <div class="mt-4 flex items-center">
              <span class="inline-block w-40">Prix:</span>
              <span
                class="text-3xl text-brand-strong-green">${res.vetement.prix}</span>
            </div>

            <!-- Categorie -->
            <div class="mt-4 flex items-center">
              <span class="inline-block w-40">Categorie:</span>
              <span
                class="text-white text-sm uppercase tracking-wider inline-block
                  px-4 py-1 rounded-full bg-gradient-to-r
                  from-brand-strong-orange to-brand-weaker-orange">{res.vetement.categories.nom}</span>
            </div>

            <!-- Pour -->
            <div class="mt-4 flex items-center">
              <span class="inline-block w-40">Pour:</span>
              <span
                class="text-white text-sm uppercase tracking-wider inline-block
                  px-4 py-1 rounded-full bg-gradient-to-r from-brand-strong-blue
                  to-brand-weak-blue">{res.vetement.pour}</span>
            </div>

            <!-- Tailles -->
            {#if res.vetement.tailles}
              <div class="flex flex-wrap">
                {#each res.vetement.tailles as taille}
                  <div
                    class="mr-3 mt-3 px-3 rounded-full border border-gray-400
                      text-brand-weaker-green text-sm uppercase tracking-wider
                      bg-gray-100">
                    {taille.taille}
                  </div>
                {/each}
              </div>
            {/if}

            <!-- Adding to Cart -->
            <div class="mt-8">
              {#if isInCart}
                <button
                  on:click={onRemoveFromCart}
                  class="flex items-center btn-blue"
                  style="width: 100%;">
                  <i class="fas fa-minus-circle mr-3" /> Retirer du Panier
                </button>
              {:else}
                <button
                  on:click={() => onAddToCart({
                      id: params.id,
                      qty: 1,
                      prix: res.vetement.prix,
                      nom: res.vetement.nom,
                      url: res.vetement.photos[0].url,
                    })}
                  class="flex items-center btn-primary"
                  style="width: 100%;">
                  <i class="mr-3 fas fa-cart-plus" /> Ajouter Au Panier
                </button>
              {/if}
            </div>

            <!-- Share on whatsapp -->
            <div class="mt-4">
              <a
                href={`whatsapp://send?&text=${document.title} ${window.location
                  .toString()
                  .replace('#', '%23')}`}
                target="_blank"
                class="flex items-center btn-secondary"
                style="width: 100%;">
                <i class="fab fa-whatsapp" /> Partager sur Whatsapp
              </a>
            </div>

            <!-- Copy the link -->
            <div class="mt-4">
              <button
                on:click={() => {
                  navigator.clipboard.writeText(window.location);
                }}
                target="_blank"
                class="flex items-center btn-yellow"
                style="width: 100%;">
                <i class="fas fa-copy mr-3" /> Copier le Lien
              </button>
            </div>
          </div>
        </div>
      {:catch error}
        <h1
          class="flex items-center mt-4 mb-2 mx-1 md:mx-auto px-4 py-8
            bg-gradient-to-r md:w-128 from-brand-strong-red to-brand-weak-red
            rounded-md shadow-md text-gray-100">
          <i class="fas fa-info-circle mr-4 tracking-normal fa-2x" />
          {error.message}
        </h1>
      {/await}
    </div>
  </Container>
</Layout>
