<script>
  import { scale } from "svelte/transition";
  import { SyncLoader } from "svelte-loading-spinners";
  import app from "../stores/app";
  import { createEventDispatcher } from "svelte";
  import Order from "../api/Order";
  import LS from "../utils/LS";
  import { cart } from "../stores/cart";

  export let show = false;
  export let loading = false;

  const infoClient = {
    nom: "",
    telephone: "",
    email: "",
    addresse: "",
    info: "",
    viderPanier: true,
    ...LS.infoClient.get(),
  };

  const dispatch = createEventDispatcher();

  const onSubmit = () => {
    loading = true;
    dispatch("orderSubmit", infoClient);
    LS.infoClient.set(infoClient);
    const orderData = {
      ...infoClient,
      contenu: $cart.map(({ id, qty }) => ({ vetement: id, quantite: qty })),
    };

    Order.send($app.endpoint, orderData)
      .then((res) => {
        console.log(res);
        loading = false;
        if (infoClient.viderPanier) {
          cart.reset();
        }
        onClose();
        alert(`Merci de votre commande! Nous vous contacterons`);
      })
      .catch((err) => {
        alert(`${err.message}`);
        console.log(err);
      });
  };

  const onClose = () => {
    dispatch("orderModalClose");
  };
</script>

{#if show}
  <div
    on:click|self={onClose}
    class="fixed top-0 left-0 bottom-0 right-0 flex items-center justify-center"
    style="z-index:999; background: rgba(0,0,0,.5)">
    <div
      transition:scale
      class="w-11/12 md:w-128 bg-gray-100 rounded-lg shadow-lg pt-4 pb-4 px-5
        border-t-2 border-brand-strong-orange overflow-auto"
      style="height: 85vh">
      <!-- <h1 class="text-gray-600 mb-2">Informations supplementaires</h1> -->

      <input
        required
        bind:value={infoClient.nom}
        type="text"
        class="mt-2 shadow-sm"
        placeholder="Nom Complet" />
      <input
        required
        bind:value={infoClient.telephone}
        type="text"
        class="mt-2 shadow-sm"
        placeholder="Telephone" />
      <input
        bind:value={infoClient.email}
        type="email"
        class="mt-2 shadow-sm"
        placeholder="Email" />
      <textarea
        rows="3"
        required
        bind:value={infoClient.addresse}
        type="text"
        class="mt-2 shadow-sm"
        placeholder="Address" />
      <textarea
        rows="3"
        type="text"
        bind:value={infoClient.info}
        class="mt-2 shadow-sm"
        placeholder="Info supplementaire a inclure sur votre commande (Optionel)" />

      <label class="mt-2 flex items-center">
        <input
          bind:checked={infoClient.viderPanier}
          type="checkbox"
          class="form-checkbox text-brand-strong-green h-6 w-6" />
        <span class="ml-3">Vider mon panier après commande</span>
      </label>

      <div class="mt-4 flex items-center justify-center">
        {#if loading}
          <div class="flex items-center p-2 w-full">
            <SyncLoader size="25" color="#F47C01" unit="px" />
            <span class="ml-4">Veuillez Patienter un moment...</span>
          </div>
        {:else}
          <button
            class="btn-secondary mr-4"
            on:click={onSubmit}>Confirmer</button>
          <button class="btn-red" on:click={onClose}>Annuler</button>
        {/if}
      </div>
    </div>
  </div>
{/if}
