<script>
  import { slide } from "svelte/transition";
  import { link } from "svelte-spa-router";
  import { cart } from "../stores/cart";
  let menuVisible = false;
  let toggleMenu = () => {
    menuVisible = !menuVisible;
  };
  let closeMenu = () => {
    menuVisible = false;
  };

  export let showOnlyHome = false;
</script>

<div
  class={`block lg:hidden px-4 pt-2 pb-1 rounded-md shadow-sm bg-gray-100 relative`}
  style="z-index:999;">
  <button class="focus:outline-none" on:click={toggleMenu}>
    <svg
      xmlns="http://www.w3.org/2000/svg"
      class="w-6 h-5 stroke-current"
      viewBox="0 0 30 21">
      <defs>
        <style>
          .abc {
            fill: none;
            stroke-linecap: round;
            stroke-linejoin: round;
            stroke-width: 3px;
          }
        </style>
      </defs>
      <g transform="translate(-3 -7.5)">
        <path class="abc" d="M4.5,18h27" />
        <path class="abc" d="M4.5,9h27" />
        <path class="abc" d="M4.5,27h27" />
      </g>
    </svg>
  </button>

  {#if menuVisible}
    <div
      transition:slide={{ duration: 500 }}
      class={`absolute mt-16 top-0 right-0 w-56 flex flex-col px-3 pt-4 pb-2 rounded-md shadow-md bg-gray-100 border-t-4 border-brand-strong-orange`}>
      <a class="mb-5" on:click={closeMenu} href="/cart" use:link>Panier ({$cart.length || 0})</a>
      <a class="mb-5" on:click={closeMenu} href="/search" use:link>Recherche</a>
      {#if !showOnlyHome}
        <a class="mb-5" on:click={closeMenu} href="#collection">Categories</a>
        <a class="mb-5" on:click={closeMenu} href="#recent">Arrivages</a>
        <a class="mb-5" on:click={closeMenu} href="#contact">Contact</a>
      {/if}
    </div>
  {/if}
</div>
